.operation{
    
    &.short{
        min-height: 100px;
        
    }
    color: black;
    min-height: 100px;
    width: 300px;
    padding: 10px;
    border-radius: 10px;
    background-color: white;
    &__header{
        align-items: center;
        justify-content: space-between;
        font-size: 1.3rem;
        padding-bottom: 20px;
        border-bottom: 0.5px solid rgba(128, 128, 128, 0.459);
    }
    &__info{
        gap: 5px;
        span{
            font-size: 1rem;
            color: grey;
        }
    }
    &__amount{
        font-weight: 700;
    }
    &__description{
        font-size: 1rem;
        padding-top: 10px;
    }
}